<template>
	<div>
		<van-nav-bar
		  fixed
		  border
		  title="公司入驻"
		  left-arrow
		  @click-left="goBack"
		/>
		<van-cell-group class="bottm">
			<van-field
			  required
			  label="logo"
			  readonly
			>
			  <template #button>
				  <van-uploader :after-read="uploadLogo" accept="image/*">
					<van-button v-if="!tled.logo" size="small" class="btnColor" type="danger" >上传图片</van-button>
					<img
					  v-else
					  class="uploaded-img"
					  :src="tled.logo"
					  alt=""
					/>
				 </van-uploader>
			  </template>
			</van-field>
		  <van-field class="companyName" required clearable  input-align="right" @input="ChangVal" v-model="tled.companyName"  label="公司全称" placeholder="请输入公司全称" />
		  <van-field  required clearable readonly input-align="right" v-model="tled.duty_paragraph"  label="公司税号" placeholder="请输入公司税号" />
		  <!-- tled.industry -->
		  <van-field
		    required
		    v-model="selectedIndustry"
		    is-link
		    readonly
		    label="公司行业"
		    placeholder="请输入公司行业"
		    input-align="right"
		    error-message-align="right"
		    @click="showIndustryPopup"
		  />
		  <van-cell required title="主营业务/公司产品介绍" :border="false" />
		  <van-field
		    class="textarea"
		    type="textarea"
		    placeholder="公司主营业务"
			v-model="tled.business"
		  />
		  <van-cell required title="公司地址" :border="false" />
		  <van-field
		    class="textarea"
		    type="textarea"
		    placeholder="公司地址"
			v-model="tled.address"
		  />
		  <van-cell required title="公司简介" :border="false" />
		  <van-field
		    class="textarea"
		    type="textarea"
		    placeholder="公司简介"
			v-model="tled.brief"
		  />
		  <!-- <uploadImg /> -->
		  <van-cell  title="公司图片或产品图片" :border="false" />
		  <van-uploader class="lf" v-model="fileList"  :max-count="4" :after-read="uploadProduct" accept="image/*"/>
		  <van-field v-model="tled.username" required clearable input-align="right" label="联系人" placeholder="请输入联系人" />
		  <van-field v-model="tled.phone" required clearable  input-align="right" label="联系电话" placeholder="请输入联系电话" />
		  <van-field
		    required
		    label="入驻协议"
		    readonly
		  >
		    <template #button>
		  	  <van-uploader :after-read="uploadFile" >
		  		<van-button v-if="!fileNmae" class="btnColor" size="small" type="danger" >上传入驻协议</van-button>
				<span v-else>{{fileNmae}}</span>
		  	 </van-uploader>
		    </template>
		  </van-field>
		</van-cell-group>
		<!-- <van-popup v-model="visible" position="bottom" :overlay="true">
		  <van-picker 
		  :columns="columns"
		   @cancel="onCancel"
		   cancel-button-text="清除选择"
		   confirm-button-text="确认"
		   @confirm="onConfirm"
		   show-toolbar
		/>
		</van-popup> -->
		<van-row class="list" type="flex" justify="center" v-show="showSettled">
		  <van-col span="24">
			  <div class="items" v-for="(item,index) in EnterpriseInfo" :key="index" @click="chVal(index)">
				  <p>名称:{{item.companyName}}</p>
				  <p>税号:{{item.duty_paragraph}}</p>
			  </div>
		  </van-col>
		</van-row>
		<van-row type="flex" justify="space-between" >
			<van-col span="12">
				<van-button to="/suppliers" type="default" size="large"  block >取消</van-button>
			</van-col>
			<van-col span="12">
				<van-button type="danger" class="btnColor" size="large"  block @click="save">保存</van-button>
			</van-col>
		</van-row>
		
		<industry-popup
		  ref="industryPopup"
		  :value="selectedIndustry"
		  @confirm="selectIndustry"
		  @clear="clearIndustry"
		/>
	</div>
</template>

<script>
function debounce(func, wait=1000){
 let timeout;
 return function(event){
  clearTimeout(timeout)
  timeout = setTimeout(()=>{
   func.call(this, event)
  },wait)
 }
}
import axios  from "axios"
import { uploadImage } from "@/api/v3.js";
import { companyCreate } from "@/api/v4.js";
import IndustryPopup from "../components/industry-popup";
import uploadImg from "@/components/uploadImg"
import {
  NavBar,
  Icon,
  Cell,
  CellGroup,
  Field,
  Uploader,
  Row,
  Col,
  Checkbox,
  Tag,
  Button,
  Picker, Popup
} from "vant";	
export default{
	components: {
	  [NavBar.name]: NavBar,
	  [Button.name]: Button,
	  [CellGroup.name]: CellGroup,
	  [Cell.name]: Cell,
	  [Field.name]: Field,
	  [Uploader.name]: Uploader,
	  [Row.name]: Row,
	  [Col.name]: Col,
	  [Icon.name]: Icon,
	  [Tag.name]: Tag,
	  [Checkbox.name]: Checkbox,
	  [Picker.name]: Picker,
	  [Popup.name]: Popup,
	  uploadImg,
	  IndustryPopup
	},
	data(){
		return{
			showSettled:false,
			value:'',
			visible:false,
			columns: [],
			EnterpriseInfo:[],
			fileList:[],
			selectedIndustryObj: [],
			industry_id:'',
			is_join:0,
			fileNmae:"",
			tled:{
				companyName:"",
				duty_paragraph:"",
				business:"",
				address:"",
				brief:"",
				productImg:[],
				logo:"",
				username:"",
				phone:"",
				file:""
			},
		}
	},	
	created() {
		const url = 'http://api.qianzhan.com/OpenPlatformService/GetToken'
		axios.get(url,{
			params:{
			type:"json",
			appkey:"0a0e7a4ea774bab7",
			seckey:"942959a69431b1ad"}
		}).then(res=>{
			sessionStorage.setItem("qzToken",res.data.result.token)
		})
	},
	methods:{
		// 点击返回按钮
		goBack() {
		  if (this.from) {
		    this.$router.back();
		  } else {
		    this.$router.replace("/demands");
		  }
		},
		// 打开需求行业选择器
		showIndustryPopup() {
		  this.$refs["industryPopup"].show();
		},
		// 选择需求行业
		selectIndustry(val) {
		  this.selectedIndustryObj = val;
		  this.industry_id = [this.selectedIndustryObj[0].id,this.selectedIndustryObj[1].id]
		  console.log(this.industry_id.join(","))
		},
		
		clearIndustry() {
		  this.selectedIndustryObj = [];
		},
		//模糊查询
		changs:debounce(function(e){
			this.ChangVal()
		}),
		ChangVal(){
			if(this.tled.companyName != ""){
				const url = 'http://api.qianzhan.com/OpenPlatformService/OrgCompanyQuery'
				axios.get(url,{
					params:{
					type:"json",
					token:sessionStorage.getItem("qzToken"),
					companyName:this.tled.companyName,
					page:1,
					pagesize:5,
					querySet:1
					}
				}).then(res=>{
					console.log(res)
					this.EnterpriseInfo = res.data.result
					// this.columns = res.data.result.map(item=>item.companyName)
					if(this.EnterpriseInfo.length>0){
						this.showSettled = true
					}
				})
			}
			
		},
		chVal(i){
			this.tled.duty_paragraph = this.EnterpriseInfo[i].creditCode
			this.tled.companyName = this.EnterpriseInfo[i].companyName
			this.tled.address = this.EnterpriseInfo[i].address
			this.tled.business = this.EnterpriseInfo[i].bussinessDes
			this.showSettled = false
		},
		async uploadFile({ file }) {
		  const data = new FormData();
		  data.append("file", file);
		  const toast = this.$toast.loading({
		    duration: 0,
		    forbidClick: true,
		    loadingType: "spinner",
		    message: "上传中"
		  });
		  try {
		    // const res = await uploadImage(data);
			this.tled.file = file
			this.fileNmae = file.name
		    toast.clear();
		  } catch (error) {
		    toast.clear();
		    this.$toast.fail(error.message);
		  }
		},
		async uploadProduct({ file }) {
		  const data = new FormData();
		  data.append("image", file);
		  const toast = this.$toast.loading({
		    duration: 0,
		    forbidClick: true,
		    loadingType: "spinner",
		    message: "上传中"
		  });
		  try {
		    const res = await uploadImage(data);
		    toast.clear();
		    this.tled.productImg.push(res.url);
			console.log(this.tled.productImg)
		  } catch (error) {
		    toast.clear();
		    this.$toast.fail(error.message);
		  }
		},
		async uploadLogo({ file }) {
		  const data = new FormData();
		  data.append("image", file);
		  const toast = this.$toast.loading({
		    duration: 0,
		    forbidClick: true,
		    loadingType: "spinner",
		    message: "上传中"
		  });
		  try {
		    const res = await uploadImage(data);
		    toast.clear();
		    this.tled.logo = res.url;
		  } catch (error) {
		    toast.clear();
		    this.$toast.fail(error.message);
		  }
		},
		save(){
			this.is_join =  1
			console.log(this.tled.file)
			companyCreate({
				logo:this.tled.logo,
				company_name:this.tled.companyName,
				duty_paragrap:this.tled.duty_paragraph,
				business:this.tled.business.replace(/\r\n/g, "<br/>").replace(/\n/g, "<br/>").replace(/\s/g, "&nbsp;"),
				address:this.tled.address.replace(/\r\n/g, "<br/>").replace(/\n/g, "<br/>").replace(/\s/g, "&nbsp;"),
				detail:this.tled.brief.replace(/\r\n/g, "<br/>").replace(/\n/g, "<br/>").replace(/\s/g, "&nbsp;"),
				img:this.tled.productImg.length>1?this.tled.productImg.join(","):this.tled.productImg,
				username:this.tled.username,
				phone:this.tled.phone,
				industry_id:this.industry_id.length>1?this.industry_id.join(","):this.industry_id,
				is_join:this.is_join,
				file:this.tled.file
			}).then(res=>{
				console.log(res)
				this.$dialog.alert({
				  title: "温馨提示",
				  message: res.msg,
				  confirmButtonText: "确认"
				}).then(()=>{
					this.$router.push("/suppliers")
				})
			}).catch(err=>{
				console.log(err)
				this.$dialog.alert({
				  title: "错误提示",
				  message: err,
				  confirmButtonText: "确认"
				});
			})
		}
		// onCancel(){
		// 	this.visible = false
		// },
		// onConfirm(val,index){
		// 	console.log(val,index)
		// 	this.tled.duty_paragraph = this.EnterpriseInfo[index].duty_paragraph
		// 	this.tled.companyName = this.EnterpriseInfo[index].companyName
		// 	this.visible = false
		// }
	},
	computed:{
		selectedIndustry() {
		  return this.selectedIndustryObj.length > 1
		    ? this.selectedIndustryObj.map(item => item.name).join("/")
		    : "";
		}
	}
}	
</script>

<style lang="less" scoped>
.btnColor{
	background:linear-gradient(90deg,rgba(196,0,36,1),rgba(217,10,32,1));
}		
.lf{
	margin-left: 15px;
}	
.bottm{
	margin-bottom:20px;
}	
.page{
	background-color: #F8F8F8 !important;
}	
.companyName{
	position: relative;
}	
.list{
	width: 90%;
	margin:0 auto;
	position: absolute;
	top: 140px;
	left:50%;
	z-index: 999;
	background-color: #F8F8F8;
	transform: translateX(-50%);
	border:1px solid #666;
	.items{
		border-bottom: 1px solid #333;
		p{
			margin:5px;
		}
	}
	.items:last-child{
		border-bottom:none;
	}
}	
.uploaded-img{
	width: 80px;
	height: 60px;
	object-fit: cover;
}
</style>
