<template>
	<!-- <van-uploader v-model="fileList" :max-size="4" multiple :after-read="uploadProduct" accept="image/*" /> -->
	<van-uploader v-model="fileList" multiple :max-count="2" :after-read="uploadProduct" accept="image/*"/>
</template>

<script>
	import {
	  Uploader,
	} from "vant";	
import { uploadImage } from "@/api/v3.js";	
export default{
	components:{
		[Uploader.name]: Uploader,
	},
	data(){
		return{
			fileList:[],
		}
	},
	methods:{
		async uploadProduct({ file }) {
		  const data = new FormData();
		  data.append("image", file);
		  const toast = this.$toast.loading({
		    duration: 0,
		    forbidClick: true,
		    loadingType: "spinner",
		    message: "上传中"
		  });
		  try {
		    const res = await uploadImage(data);
		    toast.clear();
		    let imgArr = res.url;
			console.log(imgArr)
		  } catch (error) {
		    toast.clear();
		    this.$toast.fail(error.message);
		  }
		},
	}
}	
</script>

